.errorMessage1 {
    color: white;
    background: rgba(0, 106, 221, 0.2);
    font-weight: bold;
}

.errorMessage2 {
    color: yellow;
    font-weight: bold;
}

.displayWrapper {
    background: rgba(0, 106, 221, 0.4);
    border-radius: 3%;
}
.displayFont {
    color: yellow;
}
.verticalAlign {
    display: flex;
    align-items: center;
}
.image {
    height: 60px;
}

@media only screen and (min-width: 850px) {
    .displayWrapper {
        box-shadow: 20px 10px 5px 12px rgba(1, 10, 19, 0.4);
    }
    .image {
        height: 80px;
    }
}
